@font-face {
  font-family: "CMU Serif";
  font-style: roman;
  font-weight: 500;
  src: url("./fonts/cmu-serif-500-roman.woff2") format("woff2"), url("./fonts/cmu-serif-500-roman.ttf") format("truetype");
}

@font-face {
  font-family: "CMU Serif";
  font-style: italic;
  font-weight: 500;
  src: url("./fonts/cmu-serif-500-italic.woff2") format("woff2"), url("./fonts/cmu-serif-500-italic.ttf") format("truetype");
}

@font-face {
  font-family: "CMU Serif";
