  font-weight: 700;
  src: url("./fonts/cmu-serif-700-roman.woff2") format("woff2"), url("./fonts/cmu-serif-700-roman.ttf") format("truetype");
}

@font-face {
  font-family: "CMU Serif";
  font-style: italic;
  font-weight: 700;
  src: url("./fonts/cmu-serif-700-italic.woff2") format("woff2"), url("./fonts/cmu-serif-700-italic.ttf") format("truetype");
}